export default {
    // 获得details_url
    getDetailsUrl(state, url) {
        state.details_url = url;
    },

    // 登录状态设置
    setIsLogin(state, flag) {
        state.isLogin = flag;
    },

    getItemId(state, id) {
        state.item_id = id;
    },

    // 设置头像
    getUserImg(state) {
        state.userImg = localStorage.getItem('userImg');
        state.loginName = localStorage.getItem('loginName');
    },

    // 获取标签栏数据
    getCategoryData(state, data) {
        state.categoryData = data;

        data.forEach((v, i) => {
            if (i < 16) {
                state.showData.push(v);

            } else {
                state.hideData.push(v);
            }
        });

        

    },

    // 把显示区的放到隐藏区
    showToHide(state, index) {
        state.hideData.push(state.showData[index]);

        state.showData.splice(index, 1);
    },

    // 把隐藏区的放到显示区
    hideToShow(state, index) {
        state.showData.push(state.hideData[index]);

        state.hideData.splice(index, 1);
    }
}