import axios from 'axios'
import Vue from 'vue'
export default {

    // 查询是否登录
    queryLogin({
        commit
    }) {
        
        let un = localStorage.getItem('username');
        let pw = localStorage.getItem('password');
        if (un && pw) {
            axios({
                    url: "http://www.xcodeyun.cn:10620/find",
                    method: "post",
                    data: {
                        username: un,
                        password: pw
                    }
                }).then(res => {
                    if (res.data.code == 200) {

                        // 已登录状态
                        commit('setIsLogin', true);

                        // 获得头像
                        commit('getUserImg');

                    } else {
                        // 未登录状态
                        commit('setIsLogin', false);
                    }
                })
                .catch(err => {
                    // 未登录状态
                    commit('setIsLogin', false);
                });
        } else {
            // 未登录状态
            commit('setIsLogin', false);
        }
    },

    // 头条资讯分类id
    getCategoryData({
        commit
    }) {
        axios({
                url: "/category",
                params: {
                    apikey: '7E569FB66F5262576A0C74BCB9BB1337'
                }
            })
            .then(res => {
                // 

                if (res.data.retcode == 2000) {
                    res.data.data.forEach((v, i) => {
                        v.isActive = i == 0;
                    });

                    // 储存数据
                    commit("getCategoryData", res.data.data);
                }
            })
            .catch(err => {
                
            });
    }

}